export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/Home'
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      meta: {
        title: 'Home'
      },
    },
    {
      path: '/Products',
      name: 'Products',
      component: Products,
      meta: {
        title: 'Products'
      },
    },

    {
      path: '/video',
      name: 'video',
      component: video,
      meta: {
        title: 'video'
      },
    },

    {
      path: '/News',
      name: 'News',
      component: News,
      meta: {
        title: 'News'
      },
    },

    {
      path: '/FAQS',
      name: 'FAQS',
      component: FAQS,
      meta: {
        title: 'FAQS'
      },
    },

    {
      path: '/contact',
      name: 'contact',
      component: contact,
      meta: {
        title: 'contact'
      },
    },

    {
      path: '/News/torsion-spring',
      name: 'torsionspring',
      component: torsionspring,
      meta: {
        title: 'torsionspring'
      },
    },

    {
      path: '/News/DVT-Compression-Spring',
      name: 'DVTCompressionSpring',
      component: DVTCompressionSpring,
      meta: {
        title: 'DVTCompressionSpring'
      },
    },

    {
      path: '/News/Ningbo-Fenghua-DVT-Spring-Co-ltd',
      name: 'NingboFenghuaDVTSpringColtd',
      component: NingboFenghuaDVTSpringColtd,
      meta: {
        title: 'NingboFenghuaDVTSpringColtd'
      },
    },

    {
      path: '/Products/Customized-Compression-Springs',
      name: 'CustomizedCompressionSprings',
      component: CustomizedCompressionSprings,
      meta: {
        title: 'CustomizedCompressionSprings'
      },
    },

  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})

